const noteTable: Record<string, number> = {
  "0": 0b000,
  "1": 0b001,
  "2": 0b010,
  "3": 0b011,
  "-1": 0b101,
  "-2": 0b110,
};

export const enc3bit = (val: number) => noteTable[val.toString()];

export const dec3bit = (val: number) => {
  const hit = Object.entries(noteTable).find(([k, v]) => v === val);
  if (hit && hit.length > 0) {
    return Number(hit[0]);
  }
};

